import { NumberInput, TextInput, Button, Select, ModalWindow, Root, MultilineInput, DestroyingPatternContentSwitcher } from "@tblabs/truffle";
import { Router } from './Router';
import { SlideFactory } from './SlideFactory';
import { iPhoneSlides } from './Slides/iPhone';
import { msSlides } from './Slides/ms';

NumberInput.DefaultCssClasses = "uk-input uk-form-width-small";
TextInput.DefaultCssClasses = "uk-input";
Button.DefaultCssClasses = "uk-button uk-button-default";
Select.DefaultCssClasses = "uk-select";
ModalWindow.DefaultCssClasses = "tf-modal-window";
ModalWindow.Hook = Root.Hook;
MultilineInput.DefaultCssClasses = "uk-textarea";


const slides = [
    ...iPhoneSlides,
    ...msSlides,
    {
        Id: "start",
        Type: "choose",
        Title: `### Samouczek

Co chcesz uruchomi?`,
        Options: [
            { Label: "Mikrokamere", ImageUrl: "tx.jpg", GoTo: "choose-phone" },
            { Label: "Mikrosuchawke", ImageUrl: "ms.jpg", GoTo: "ms-start" },
        ]
    },
    {
        Id: "choose-phone",
        Type: "choose",
        Title: "Jaki masz telefon?",
        Options: [
            { Label: "iPhone", ImageUrl: "iphone/iphone.jpg", GoTo: "iPhone-settings" },
            { Label: "Android", ImageUrl: "android/android.jpg", GoTo: "android" },
        ]
    },
    {
        Id: "android",
        Type: "page",
        Content: `### Android
        
* Ustawienie hotspota w systemie Android moe wyglda inaczej w zalenoci od jego wersji i producenta telefonu. Kolejne kroki maj charakter jedynie pogldowy*

Rozwi menu grne i znajd co co przypomina hotspot lub router osobisty (moe nazywa si \`Hotspot\` lub \`Router\` lub jako inaczej).  
Przytrzymaj te opcje i wejd do ustawie.
`,
        ImageUrl: "android/android-1.jpg",
        NextId: "android-hotspot"
    },
    {
        Id: "android-hotspot",
        Type: "page",
        Content: `W polu \`Nazwa hotspota\`, \`SSID\` lub podobnym wpisz *hotspot1*. Wielko znakw ma znaczenie! Nie pomyl si!  

W polu \`Haso\` wpisz *hotspot1password*. Wielko znakw ma znaczenie!

Pozostae opcje raczej nie maj znaczenia, ale jeli gdzie w menu zauwaysz opcje *wstecznej kompatybilnoci* lub podone - wcz je.  
Szyfrowanie naley ustawi na *WPA2 PSK*. Warto te wyczy takie opcje jak *automatyczny hotstpot* czy *automatyczne wyczanie*.
`,
        ImageUrl: "android/android-2.jpg",
        NextId: "led"
    },
    {
        Id: "camera-start",
        Type: "page",
        Content: "Hotspot jest ju gotowy. Te ustawienia wprowadza si tylko raz.\n\nOd tej pory wystarczy wczy hotspot przed uruchomieniem kamery (guzikiem `Dopuszczaj innych`).\n\nW kolejnym kroku wczymy kamere...",
        ImageUrl: "",
        NextId: "led"
    },
    {
        Id: "iPhone:end-x",
        Type: "page",
        Content: "Podcz kamere do prdu i odczekaj minute.\nPrzy gniazdku zasilania kamery znajduje si dioda, jeli miga na:\n\n zielono - wszystko jest ok\n\n czerwono - hotspot nie dziaa (upewnij si, e poprzednie kroki zostay wykonane poprawnie)\n\n---\n *Pamitaj by pozostawi telefon na tym ekranie hotspota na czas uruchamiania kamery*",
        ImageUrl: "iphone/hot.png",
        NextId: "led"
    },
    {
        Id: "led",
        Type: "choose",
        Title: `### Uruchomienie kamery
Podcz kamere do powerabanka. Mona uy dowolnego kabla mini-USB.

![Poczenie](conn.jpeg)

Odczekaj minute a kamera si uruchomi.  

Przy gniazdku zasilania kamery znajduje si dioda.\n\nNa jaki miga kolor?`,
        Options: [
            { Label: " Zielony", ImageUrl: "green.jpeg", GoTo: "led-green" },
            { Label: " Czerwony", ImageUrl: "red.jpeg", GoTo: "red-led" },
            { Label: "wiato cige", ImageUrl: "red.jpeg", GoTo: "const-led" },
            { Label: " Nic nie miga", ImageUrl: "noled.jpeg", GoTo: "no-led" },
        ]
    },
    {
        Id: "red-led",
        Type: "page",
        Content: ` Czerwona migajca dioda oznacza, e kamera pracuje poprawnie, ale nie moe si poczy z serwerem.  
Powodw moe by kilka:
- *Hotspot nie jest wczony lub nie dziaa poprawnie* - upewnij si, e dziaa prawidowo sprawdzajc czy dziaa pod odpowiedni nazw oraz czy "widz go" inne urzdzenia, np. komputer,
- *Hotspot nie udostpnia internetu* - upewnij si, e transmisja danych w telefonie jest wczona oraz e rozmowa telefoniczna nie blokuje transmisji,
- *Hotspot jest poza zasigiem* - kamera musi znajdowa si w odlegoci maksymalnie kilku metrw od telefonu`,
        ImageUrl: "",
        NextId: "contact"
    },
    {
        Id: "no-led",
        Type: "page",
        Content: `Kamera potrzebuje okoo 50 sekund aby si uruchomi. Brak wiata moe oznacza, e:
- *Nie upyneo jeszcze do czasu* - odczekaj co najmniej minute,
- *Brak zasilania* - sprawd czy powerbank dziaa, powinna si pali na nim jaka dioda,
- *Uszkodzony przewd USB* - naley wymieni na dowolny inny

Jeli to adna z powyszych przyczyn - skontaktuj si z nasz infolini.`,
        ImageUrl: "",
        NextId: "contact"
    },
    {
        Id: "const-led",
        Type: "page",
        Content: `W teorii wiato cige diody kontrolnej nie jest moliwe. Upewnij si, e patrzysz na waciw lampke - t przy gniedzie zasilania. Jeli jeste absolutnie pewien, e to waciwa dioda, pozostaje tylko jedna moliwo - kamera si zawiesia. Odcz j od prdu i podcz ponownie. Zgo problem na infolinii poniewa nie powinien nigdy wystpi.`,
        ImageUrl: "",
        NextId: "contact"
    },
    {
        Id: "led-green",
        Type: "choose",
        Title: ` Zielona migajca dioda oznacza, e kamera poprawnie nawizaa poczenie z serwerem.
        
Wejd na stron odbiornika, ktrej adres znajdziesz w *SMS od nas* lub na *karcie doczonej do zestawu*.  
    
 *Jeli nie otrzymae hase - zadzwo i popro o ich wygenerowanie!*

Zaloguj si i sprawd czy wszystko jest w porzdku. Jeli nie - wybierz ktr z poniszych opcji:`,
        Options: [
            { Label: "Brak obrazu", ImageUrl: "", GoTo: "kamera:brak-obrazu" },
            { Label: "Niewyrany obraz", ImageUrl: "", GoTo: "kamera:niewyrazny-obraz" },
            { Label: "Powolna transmisja", ImageUrl: "", GoTo: "kamera:powolna-transmisja" },
            { Label: "Za maa rozdzielczo", ImageUrl: "", GoTo: "kamera:mala-rozdzielczosc" },
        ]
    },
    {
        Id: "kamera:brak-obrazu",
        Type: "page",
        Content: `### Brak obrazu 

Jeeli po zalogowaniu si na stronie nie widzisz podgldu z kamery sprawd:
- *Czy obiektyw nie jest zasonity?* - odso go,
- *Czy trasnsmisja jest wczona?* - w prawym grnym rogu jest wybierak rozdzielczoci, sprawd czy nie jest w pozycji \`Nadawanie wstrzymane\`. Pod ramk podgldu znajduje si czerwony guzik \`Przywr nadawanie\`. Kliknij go aby kamera zacza pracowa.

Obserwuj diod na kamerze. Jeli zacza miga na czerwono oznacza to problem z hotspotem. Sprawd czy ten na pewno dziaa.
`,
        NextId: "contact"
    },
    {
        Id: "kamera:niewyrazny-obraz",
        Type: "page",
        Content: `### Rozmazany obraz
        
Rozmazany obraz moe oznacza tylko jedno: obiektyw kamery zosta przestawiony. Kamera ma sta ogniskow i mona j zmienia obracajc guziczkiem. Trzymajc kamer 30-40cm od kartki, kr powoli guzikiem tak by tekst sta si dobrze widoczny. Rb to ostronie poniewa to delikatny element. Staraj si nie wykrci guzika zupenie jak i nie wkrci go za mocno. Zalecamy ruchy po 2-3mm.

Zwr rwnie uwag na to by podczas montau w odziey guzik nie obrci si samoistnie.
`,
        NextId: "contact"
    },
    {
        Id: "kamera:powolna-transmisja",
        Type: "page",
        Content: `### Powolna transmisja
        
Kamera wysya zdjcia co maksymalnie 2-3 sekundy. Nie da si szybciej. Taka forma transmisji gwarantuje te sam jako zdj za kadym razem niezalenie od jakoci poczenia z internetem.  
Jeli natomiast czas przesania zdjcia przekracza 5 sekund moe to oznacza, e:
- *Hotspot jeszcze si rozgrzewa* - dotyczy to gwnie iPhone'w ktre czsto na pocztu dziaaj wolniej,
- *Internet dziaa zbyt wolno* - i moe to zalee od miejsca, w ktrym jeste, 
- *Internet koczy si* - sprawd czy masz wystarczajc ilo pakietw danych,
- *Kiepski zasig danego operatora* - by moe inny operator bdzie mia lepszy zasig w tym miejscu,
- *Operator ogranicza internet na czas rozmowy* - niektrzy operatorzy ograniczaj transmisje danych podczas rozmowy telefonicznej. Rozcz sie i sprawd jak zareaguje kamera,
- *Ustawiona jest zbyt dua rozdzielczo* - i wace duo zdjcia potrzebuj wicej czasu na przesanie, ich rozdzielczo mona zmieni wybierakiem w prawym grnym rogu aplikacji do podgldu,
`,
        NextId: "contact"
    },
    {
        Id: "kamera:mala-rozdzielczosc",
        Type: "page",
        Content: `### Za maa rozdzielczo
        
Rozdzielczo zdj mona ustawi wybierakiem w prawym grnym rogu aplikacji:
- \`Niska jako\` - 400x300px, szybka transmisja w maej rozdzielczoci,
- \`Jako optymalna\` - 1200x900px, idealna pod kartke papieru,
- \`Wysoka jako\` - 1600x1200px, dobra pod ekran komputera,
- itd  

Im wiksze jest zdjcie - tym duej bdzie przesyane.  

Jeli mimo zwikszenia rozdzielczoci obraz nadal jest niewyrany oznacza to problem z ostroci kamery (opisany w rozdziale "Niewyrany obraz" w menu wyej).

`,
        NextId: "contact"
    },
    {
        Id: "contact",
        Type: "page",
        Content: `### Pomoc

Jeli nie udao Ci si samodzielnie poradzi sobie z problemem skorzystaj z opcji poniej:
        
 Infolinia: *507-293-714*  

 Strona kontaktu: [www.specteam.pl](https://specteam.pl/Kontakt)`,
    },
]

const root = new Root().Class("root")

const _router = new Router();
const _sf = new SlideFactory(_router);

root.Append(
    new DestroyingPatternContentSwitcher(_router.Hash).Class("PagesSwitcher")
        .AddContent("slide/:id", ({ id }) =>
        {
            console.log('slide', id)
            const slide = slides.find(x => x.Id == id)
            console.log(slide)
            slide
                ? _sf.Create(slide)
                : "Slide not found"
        })
        // .AddDefaultContent(() => _router.GoToStart())
        .AddDefaultContent(() => "hi")
)

